<template>
  <div class="row mt-5">
  <div class="col-md-8 mx-auto">
    <div class="accordion" role="tablist">
      <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-0" role="tab">
          <b-button block v-b-toggle.accordion-1 variant="info" class="col-12">Personal Information</b-button>
        </b-card-header>
        <b-collapse ref="1" id="accordion-1" visible accordion="my-accordion" role="tabpanel">
          <b-card-body>
            <ValidationObserver v-slot="{ invalid,handleSubmit }" >
              <ValidationProvider name="firstName" rules="required" v-slot="{ errors }">
                <label for="firstName" class="d-block">Firstname<span class="text-danger">*</span></label>
                <input id="firstName" v-model="form.firstName" type="text" class="form-control" autocomplete="firstName">
                <span class="d-block text-danger">{{ errors[0] }}</span>
            </ValidationProvider>
            <ValidationProvider name="lastName"  v-slot="{ errors }">
                <label for="lastName" class="d-block">Lastname</label>
                <input id="lastName" v-model="form.lastName" type="text" class="form-control" autocomplete="lastName">
                <span class="d-block text-danger">{{ errors[0] }}</span>
            </ValidationProvider>
            <ValidationProvider name="E-mail" rules="required|email" v-slot="{ errors }">
                <label for="email" class="d-block">Email<span class="text-danger">*</span></label>
                <input id="email" v-model="form.email" type="email" class="form-control" autocomplete="email">
                <span class="d-block text-danger">{{ errors[0] }}</span>
            </ValidationProvider>
            <ValidationProvider name="phoneNumber" rules="required|min:10|max:10" v-slot="{ errors }">
                <label for="phoneNumber" class="d-block">Phone Number<span class="text-danger">*</span></label>
                <input id="phoneNumber" v-model="form.phoneNumber" type="number" class="form-control" autocomplete="phoneNumber">
                <span class="d-block text-danger">{{ errors[0] }}</span>
            </ValidationProvider>
            
            <b-button class="mt-2 btn-info" :disabled="invalid" @click="handleSubmit(submit(form))">Next</b-button>
        </ValidationObserver>
          </b-card-body>
        </b-collapse>
      </b-card>
      <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-0" role="tab">
          <b-button block v-b-toggle.accordion-2 variant="info" class="col-12">Experience</b-button>
        </b-card-header>
        <b-collapse ref="2" id="accordion-2" accordion="my-accordion" role="tabpanel">
          <b-card-body>
            <p> Enter your experience starting from your latest one </p>
            <ValidationObserver v-slot="{ invalid,handleSubmit }" >
              <b-card class="mb-3" v-for="(e,i) of experience" :key="i" >
              <ValidationProvider :name="`company${i+1}`" rules="required" v-slot="{ errors }">
                <label :for="`company${i+1}`" class="d-block">Company<span class="text-danger">*</span></label>
                <input  v-model="e.companyName" type="text" class="form-control" autocomplete="companyName">
                <span class="d-block text-danger">{{ errors[0] }}</span>
            </ValidationProvider>
              <ValidationProvider  :name="`startDate${i+1}`" rules="required" v-slot="{ errors }" vid="startDate">
                <label :for="`startDate${i+1}`" class="d-block">Start date<span class="text-danger">*</span></label>
                <input v-model="e.startDate" type="date" class="form-control" autocomplete="startDate">
                <span class="d-block text-danger">{{ errors[0] }}</span>
            </ValidationProvider>
            <ValidationProvider :name="`endDate${i+1}`" :rules="`compare_date:${e.startDate},${maxDate}`" v-slot="{ errors }">
                <label :for="`endDate${i+1}`" class="d-block">End date<span class="text-danger">*</span></label>
                <input  :disabled="e.currentCompany==true" v-model="e.endDate" type="date" class="form-control" autocomplete="endDate">
                <span class="d-block text-danger">{{ errors[0] }}</span>
            </ValidationProvider>
            <ValidationProvider :name="`currentCompany{i+1}`" >
                <label :for="`currentCompany{i+1}`" class="d-block mt-2">
                  <input  v-model="e.currentCompany" type="checkbox" value="I currently work here" autocomplete="currentCompany">
                  I currently work here
                </label>
                <b-button v-if="i>0" class="mt-2 btn-info" @click="removeExperience(i)">Remove Experience</b-button>

            </ValidationProvider>
          </b-card>
            <span class="d-block text-danger">{{ errMsg }}</span>
            <b-button class="mt-2 btn-info" @click="addExperience">Add Experience</b-button>

            <b-button class="mt-2 mx-3 btn-info" :disabled="invalid" @click="handleSubmit(submit(expForm))">Next</b-button>
        </ValidationObserver>
          </b-card-body>
        </b-collapse>
      </b-card>

      <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-0" role="tab">
          <b-button block v-b-toggle.accordion-3 variant="info" class="col-12">Skills</b-button>
        </b-card-header>
        <b-collapse ref="3" id="accordion-3" accordion="my-accordion" role="tabpanel">
          <b-card-body>
            <ValidationObserver v-slot="{ invalid,handleSubmit }" >
              <b-card class="mb-3" v-for="(s,i) of skills" :key="i" >
                <ValidationProvider name="skill" rules="required" v-slot="{ errors }">
                  <label for="skill" class="d-block">Skill Name<span class="text-danger">*</span></label>
                  <input id="skill" v-model="s.skillName" type="text" class="form-control" autocomplete="skill">
                  <span class="d-block text-danger">{{ errors[0] }}</span>
              </ValidationProvider>
              <ValidationProvider name="skillLevel" rules="required|between:1,10" v-slot="{ errors }">
                  <label for="skillLevel" class="d-block">Skill Level<span class="text-danger">*</span></label>
                  <input id="skillLevel" v-model="s.skillLevel" type="number" class="form-control" autocomplete="skillLevel">
                  <span class="d-block text-danger">{{ errors[0] }}</span>
              </ValidationProvider>
              <b-button v-if="i>0" class="mt-2 btn-info" @click="removeSkill(i)">Remove Skill</b-button>
              </b-card>
            <b-button class="mt-2 btn-info" @click="addSkill">Add Skill</b-button>
            <b-button class="mt-2 mx-3 btn-info" :disabled="invalid" @click="handleSubmit(submit(skillForm))">Next</b-button>
            </ValidationObserver>
          </b-card-body>
        </b-collapse>
      </b-card>
    
    
    </div>
  </div>
  
    <div class="col-8 mx-auto">
      <div class="row justify-content-md-center mt-4">
      
     
       
      
      <div class="col-4 p-0 mt-2 progress-tracker">
  <b-progress :max="max" class="mb-3">
      <b-progress-bar  :value="values[0]"></b-progress-bar>
     
  </b-progress>
  <b-icon ref="icon1" icon="check-all"  font-scale="2"></b-icon>
</div>

  <div class="col-4 p-0 mt-2 progress-tracker">
  <b-progress  :max="max" class="mb-3">
      <b-progress-bar  :value="values[1]"></b-progress-bar>
    </b-progress>
    
    <b-icon ref="icon2" icon="check-all" font-scale="2"></b-icon>
    
    </div>

    
    <div class="col-4 p-0 mt-2 progress-tracker">
    <b-progress :max="max" class="mb-3 "> 
      <b-progress-bar :value="values[2]"></b-progress-bar>
    </b-progress>
  
    <b-icon ref="icon3" icon="check-all" font-scale="2"></b-icon>
  
    </div>
   
</div>
</div>
<div class="col-8 mx-auto" v-if="successMsg">
  <b-card>
   <b-card-body>{{ successMsg }}</b-card-body>
  </b-card>
</div>
</div>
</template>
  <script>
  //import * as firebase from 'firebase/database'
export default {
    name:'ApplicationForm',
    data() {
        return {
            form: {
                email: '',
                firstName: '',
                lastName: "",
                phoneNumber:'',
                skills:'',
                ref:1
            },
            expForm: {
                companyName:'',
                startDate:'',
                endDate:'',
                skills:'',
                currentCompany:'',
                ref:2,
                id:0
            },
            skillForm: {
              skillName: "",
              skillLevel: "",
              ref:3
            },
            experience:[{
                companyName:'',
                startDate:'',
                endDate:'',
                skills:'',
                currentCompany:'',
                ref:2,
                id:0
            },],
            skills:[{
              skillName: "",
              skillLevel: "",
              ref:3
            }],
            values: [0, 0, 0],
            max: 100,
            errMsg: '',
            successMsg: ''
        }
    },
    computed: {
        maxDate() {
          const today = new Date().toISOString().split("T")[0];
          return today;
        }
      },
    methods: {
        next(i) {
         
          this.$refs[i].toggle();
          
          if(i!=3)
          this.$refs[i+1].toggle();
          this.$set(this.values, i-1, 100)
          setTimeout(()=>{
            this.$refs[`icon${i}`].classList.add('active')
          },500)
         this.success()
        },
        
        submit(f) {
          f.valid = true;
          if(f.ref==2 && this.experience.length>1) {
           if(this.checkExperience()=="no overlap") {
            this.errMsg=''
            this.next(f.ref)
           }
           else {
            this.errMsg = this.checkExperience()
           }
          
        }
        else {
         this.next(f.ref);
        }
        },
        addExperience() { 
          this.experience.push(this.expForm)
        },
        removeExperience(i) {
          this.experience.splice(i,1)
        },
        addSkill() {
          this.skills.push(this.skillForm)
        },
        removeSkill(i) {
          this.skills.splice(i,1)
        },
        checkExperience() {
          for(let i=0;i<this.experience.length;i++) {
            for(let j=1;j<this.experience.length;j++) {
              if(this.experience[i].startDate <this.experience[j].endDate) {
                return "There is a overlap in the experience"
              }
              else {
                return "no overlap"
              }
            }
          }
        },
        success() {
          if(this.form.valid && this.skillForm.valid && this.expForm.valid) {
            this.successMsg= "Details captured successfully! we will get back to you soon";
          } 
        }
       
       
    }
}
</script>

